	module.exports = {"data":{"rawCamp":{"html":"<p>Cette première édition des CodingCaenCamp sera placée sous le drapeau de <a href=\"https://hacktoberfest.digitalocean.com\">Hacktoberfest</a>. Pourquoi ? Et bien pour gagner des T-shirts ! Mais surtout pour aborder cette pratique élémentaire du monde de l’open source : <a href=\"https://help.github.com/articles/about-pull-requests/\">pull request</a>.</p>\n<h2>Concrètement, on fera quoi ?</h2>\n<p>Difficile à dire, cela dépendra des personnes présentes. Mais sauf meilleures idées, nous pourrons apprendre au besoin à ceux qui ne savent pas trop comment s’y prendre à faire un pull-request sur un projet open source. Et pour ceux qui connaissent déjà, ce sera l’occasion de trouver des issues intéressantes sur des projets open source (comme le site des CaenCamp par exemple !). Pour rappel, le principe de l’Hacktoberfest, c’est de faire 5 PR sur des projets open source durant le mois d’octobre pour gagner le <a href=\"https://blog.github.com/2017-11-30-hacktoberfest-2017-the-results-are-in/\">fameux T-shirt</a>.    </p>\n<p>Enfin, ce sera l’occasion de nous rencontrer, de discuter de la suite des CodingCaenCamp, ou de la pertinence de faire cette première édition sous le drapeau de l’Hacktoberfest quand certains considèrent que <a href=\"https://www.gnu.org/philosophy/open-source-misses-the-point.fr.html\">l'open source perd de vue l'éthique du logiciel libre</a>.</p>\n<h2>Qui peut venir ?</h2>\n<p>Toute personne motivée pour s’impliquer dans un projet open source ! <strong>Attention, il ne s’agit pas de venir apprendre à coder. Ce n’est pas un cours, il n’y a pas de programme.</strong> Mais nous misons sur le fait que la somme des connaissances réunies permettra à chacun d’apprendre et de construire des choses.   </p>\n<p>Il sera évidemment beaucoup question de code, mais sans chapelle technologique. Et le code n'est pas la seule compétence induisant la réussite d’un bon projet. Il faut aussi avoir des idées, des capacités éditoriales, un talent graphique, une vision sur l’ergonomie...</p>\n<h2>Ce sera où ?</h2>\n<p>Nous serons accueillis chez <a href=\"https://www.hey-coworking.com/\">Hey! Coworking</a>, <a href=\"https://www.google.fr/maps/place/HEY+!+Coworking/@49.1790401,-0.3522317,15z/data=!4m2!3m1!1s0x0:0x424ace3e48cd2332?sa=X&#x26;ved=2ahUKEwjugr707d_dAhURJhoKHQdbANoQ_BIwDnoECAoQCw\">47 Quai de Juillet, 14000 Caen</a>, le mardi 16 octobre de 18h30 à 21h30.</p>\n<h2>Merci !</h2>\n<p>Un grand merci à <a href=\"http://knplabs.com/fr\">KNPLabs</a> (@KnpLabs) qui sera le <strong>PizzaSponsor</strong> de cette première édition.</p>","frontmatter":{"date":"2018-10-16 18:30:00","description":"Cette première édition des CodingCaenCamp sera placée sous le drapeau de Hacktoberfest. Pourquoi ? Et bien pour gagner des T-shirts ! Mais surtout pour aborder cette pratique élémentaire du monde de l’open source : la pull request.","edition":1,"image":"hacktoberfest.png","meetupId":255118278,"title":"Hacktoberfest 2018"}}},"pathContext":{"slug":"hacktoberfest-2018"}}

